@use 'base/variables' as v;
@use 'base/mixins' as m;

.agenda{

    padding: 5rem 0;
    @include m.contenedor;

}

.eventos{

    $modificador: &;

    &__heading{
        color: v.$azul;
        margin: 5rem 0;
        text-align: left;
        font-size: clamp(2rem, -.8rem + 8vw, 6rem);
    }

    &__fecha{
        color: v.$gris;
        margin: 3rem 1rem 0;
    }

    &--workshops{

        #{$modificador}__heading{
            color: v.$secundario;
        }

        .evento__informacion{
            background-color: v.$secundario;
            transition: background-color .3s ease;

            &:hover{
                background-color: darken(v.$secundario, 10);
                transition: background-color .3s ease;
            }
        }

    }

    .swiper-button-prev, .swiper-button-next{
        color: v.$blanco;
        opacity: .8;
    }

}

.evento{
    &__hora{
        font-weight: v.$bold;
    }

    &__informacion{
        padding: 2rem;
        border-radius: 1rem;
        background-color: v.$azul;
        color: v.$blanco;
        transition: background-color .3s ease;

        &:hover{
            background-color: darken(v.$azul, 10);
        }
    }

    &__nombre{
        font-size: 2rem;
        margin: 0;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
    }

    &__introduccion{
        font-size: 1.6rem;
        display: -webkit-box;
        -webkit-line-clamp: 4;
        -webkit-box-orient: vertical;
        overflow: hidden;
        transition: -webkit-line-clamp .3s ease;

        @include m.tablet {
            -webkit-line-clamp: 2;
            transition: -webkit-line-clamp .3s ease;
        }
    }

    &__autor-info{
        display: flex;
        gap: 2rem;
        justify-content: space-between;
        align-items: center;
    }

    &__imagen-autor{
        width: 5rem;
        border-radius: 50%;
        background-color: v.$blanco;
    }

    &__autor-nombre{
        font-weight: v.$bold;
    }

    &__agregar{
        @include m.boton(v.$blanco, v.$negro);
        margin-top: 1rem;

        &:disabled{
            opacity: .5;
            cursor: not-allowed;
        }
    }
}